import { isSameDay } from 'date-fns'
import { format } from 'date-fns/fp'
import numeral from 'numeral'

import {
  FAddressFragment,
  FCompanyBranchFragment,
  FUserNameFragment,
} from '../graph/generated'
import { formatWithLocale } from '../tools/datefns'

export const fullDateFormat = 'iiii PPP'
export const formatFullDate = formatWithLocale(fullDateFormat)

export const fullDateTimeFormat = 'iiii PPPp'
export const formatFullDateTime = formatWithLocale(fullDateTimeFormat)

export const dateFormat = 'd.M.yyyy'
export const formatDate = formatWithLocale(dateFormat)

export const systemTimeFormat = 'H:mm'
export const formatSystemTime = format(systemTimeFormat)

export const timeFormat = 'pp'
export const formatTime = formatWithLocale(timeFormat)

export const formatTimeWithoutTodayDate = (dateTime: Date) =>
  isSameDay(dateTime, new Date())
    ? formatTime(dateTime)
    : formatFullDateTime(dateTime)

export const numberFormat = '0[.]000'
export const formatNumber = (num: string | number) =>
  numeral(num).format(numberFormat)

export const priceFormat = '0,0.[00]'
export const formatPrice = (price: string | number, currencyCode?: string) => {
  const priceNumber = numeral(price).format(priceFormat)
  return currencyCode ? `${priceNumber} ${currencyCode}` : priceNumber
}

export const formatAddress = (address: FAddressFragment) => {
  if (!address) {
    return ''
  }
  if (!address.street) {
    if (address.city && address.houseNumber) {
      return `${address.city} ${address.houseNumber}, ${address.state}`
    }
    if (address.city) {
      if (address.state) {
        return `${address.city}, ${address.state}`
      }
      return address.city
    }
    return `${address.state}`
  }
  if (!address.houseNumber) {
    return `${address.street}, ${address.city}`
  }
  return `${address.street} ${address.houseNumber}, ${address.city}`
}

export function formatFullName(user: FUserNameFragment) {
  return `${user.lastName} ${user.firstName}`.trim()
}

export function formatBranchName(branch: FCompanyBranchFragment) {
  return branch.name
}

export function formatBranchNameWithBrand(branch: FCompanyBranchFragment) {
  return `${branch.name} [${branch.brand.name}]`
}
