import { Trans } from '@lingui/macro'
import {
  Dialog,
  DialogActions,
  DialogContent,
  Typography,
} from '@material-ui/core'
import { VolumeOff, VolumeUp } from '@material-ui/icons'
import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { TouchButton } from '../molecules/TouchButton'
import { Colors } from '../styling'
import { useRoot } from './RootProvider'
import { SizerButton } from './SizerButton'

export const SoundControl = React.memo<NoChildren>(() => {
  const { audio } = useRoot()
  return useObserver(() => (
    <>
      <SizerButton>
        <TouchButton
          id="sound-control-button"
          color={audio.enabled ? Colors.green : Colors.red}
          icon={audio.enabled ? VolumeUp : VolumeOff}
          iconSize={2}
          onClick={() => {
            audio.setUserEnabled(!audio.userEnabled)
          }}
        />
      </SizerButton>
      <Dialog open={audio.askForAudio}>
        <DialogContent>
          <Typography>
            <Trans>
              You have enabled sounds before. Due to technical restrictions, you
              need to click on the button below for sounds to be able to play.
              You can disable sounds with green button in top right corner.
            </Trans>
          </Typography>
        </DialogContent>
        <DialogActions>
          <TouchButton
            color={Colors.greyMiddle}
            onClick={() => audio.setCanPlay(true)}
          >
            <Trans>Confirm to play sounds</Trans>
          </TouchButton>
        </DialogActions>
      </Dialog>
    </>
  ))
})
