import useAudio from 'react-use/lib/useAudio'
import { HTMLMediaProps } from 'react-use/lib/util/createHTMLMediaHook'

export function useAudioPlayer(props: HTMLMediaProps) {
  const [audio, state, controls] = useAudio({
    autoPlay: false,
    loop: false,
    ...props,
  })

  return [audio, controls, state] as [
    typeof audio,
    typeof controls,
    typeof state,
  ]
}
